import React, {useEffect, useRef, useState} from "react";
import './CameraSection.css'
import {Section} from "../../shared/ui/Section/Section";
import {HStack} from "../../shared/ui/HStack/HStack";
import {Text} from "../../shared/ui/Text/Text";
import {CAMERA_DESC, TAKE_PHOTO} from "../../types/const";
import {Svg} from "../../shared/ui/Svg/Svg";
import {CommonSection} from "../../shared/ui/CommonSection/CommonSection";
import {ReactComponent as CameraSvg} from '../../shared/assets/svg/Camea.svg'
import {ReactComponent as ArrowSvg} from '../../shared/assets/svg/Arrow.svg'
import {useFaceDetection} from "../../hooks/useFaceDetection";


interface CameraProps {
    setError: any,    // ,      

    setPhotoUrl: any, //    URL  (  )
    setIsCameraOn: any
    isCameraOn: any
}

const CameraSection: React.FC<CameraProps> = ({
                                                  setPhotoUrl,
                                                  isCameraOn,
                                                  setIsCameraOn,
                                                  setError,
                                              }) => {
    const [isLoaded, setIsLoaded] = useState(false);
    const videoBorderRef = useRef<HTMLDivElement | null>(null); //    video,      
    const videoRef = useRef<HTMLVideoElement | null>(null); //    video,      
    const canvasRef = useRef<HTMLCanvasElement | null>(null); //    canvas,      
    const streamRef = useRef<MediaStream | null>(null); //    

    const {
        isFaceDetected,
    } = useFaceDetection(isCameraOn, videoRef, canvasRef, videoBorderRef)

    const handleVideoLoaded = () => {
        setTimeout(() => {
            setIsLoaded(true);
        }, 0)

    };

    useEffect(() => {
        const startVideo = async () => {
            try {
                //       API getUserMedia
                const stream = await navigator.mediaDevices.getUserMedia({
                    video: {facingMode: "user"}, //      ()
                });
                streamRef.current = stream; //     (  )
                if (videoRef.current) videoRef.current.srcObject = stream;//     video
                setPhotoUrl(null); //  URL  (  ,    )
                setError(null); //  ,   
            } catch (err) {
                //       ,  
                setError("   ");
                console.error(err);
            }
        }
        startVideo()
    }, [])

    const takePhoto = () => {
        if (canvasRef.current && videoRef.current) {
            const canvas = canvasRef.current; //    
            const context = canvas.getContext("2d"); //    
            if (context) {
                //   ,    
                canvas.width = videoRef.current.videoWidth;
                canvas.height = videoRef.current.videoHeight;
                //      
                context.drawImage(videoRef.current, 0, 0, canvas.width, canvas.height);
                //    DataURL   JPEG
                setPhotoUrl(canvas.toDataURL("image/jpeg"));
            } else {
                setPhotoUrl(null) //     ,  URL
                if (streamRef.current) {

                    //    (  ),   
                    streamRef.current.getTracks().forEach((track: { stop: () => any; }) => track.stop());
                }
            }
        } else {
            setPhotoUrl(null) //     ,  URL
            if (streamRef.current) {

                //    (  ),   
                streamRef.current.getTracks().forEach((track: { stop: () => any; }) => track.stop());
            }
        }

        setIsCameraOn(false);
    };
    return (
        <CommonSection
            max
        >
            <Section className={`video_box ${isLoaded ? '' : 'hide'}`}>

                <video
                    className="video_online"
                    ref={videoRef}
                    playsInline
                    autoPlay
                    muted
                    controls={false}
                    onLoadedMetadata={handleVideoLoaded}
                />
                <div ref={videoBorderRef} className="video_box__wrapper"/>
                <canvas
                    ref={canvasRef}
                    className={'canvas'}
                />
                {/*        */}
                <div
                    className={`face_overlay ${isFaceDetected ? 'ok' : 'err'}`}

                />
            </Section>
            <Section max>
                <HStack max>
                    <Text
                        text={CAMERA_DESC}
                        type={'text'}
                        max
                    />
                </HStack>
            </Section>
            <Section
                clickable
                max
                onClick={takePhoto}
                disabled={!isFaceDetected}
            >
                <HStack gap={'10'} max>
                    <Svg Svg={CameraSvg}/>
                    <Text
                        text={TAKE_PHOTO}
                        type={'text'}
                        max
                    />
                    <Svg width={'12px'} height={'12px'} color={'subtitle'} Svg={ArrowSvg}/>
                </HStack>

            </Section>
        </CommonSection>
    );
};

export default CameraSection
