//       base64   Blob    MIME
export const base64ToBlob = (base64: string, mimeType: string): Blob => {
    //  1:   "data:image/jpeg;base64,"   base64 (  )
    const byteString = atob(base64.split(",")[1]);
    // atob() -    JavaScript,      base64    (ASCII).

    //  2:  ArrayBuffer    
    const arrayBuffer = new ArrayBuffer(byteString.length); // ArrayBuffer   ,      .
    const uint8Array = new Uint8Array(arrayBuffer); // Uint8Array    ,   8-   ( 0  255).

    //  3:   uint8Array    byteString
    //    byteString   ,     uint8Array.
    for (let i = 0; i < byteString.length; i++) {
        uint8Array[i] = byteString.charCodeAt(i); // charCodeAt(i)  ,         i.
    }

    //  4:   Blob   uint8Array   MIME-
    // Blob   ,  ,        .
    return new Blob([uint8Array], { type: mimeType });
};
